import * as types from './mutation-types'

const mutations = {
  [types.SET_PLAY_STATE] (state, flag) {
    state.playState = flag
  },
  [types.SET_FULLSCREEN] (state, flag) {
    state.fullscreen = flag
  },
  [types.SET_SEQUENCE_LIST] (state, list) {
    state.sequenceList = list
  },
  [types.DELETE_SEQUENCE_LIST] (state, index) {
    state.sequenceList.splice(index, 1)
  },
  [types.CLEAR_SEQUENCE_LIST] (state) {
    state.sequenceList = []
  },
  [types.SET_PLAYLIST] (state, list) {
    state.playlist = list
  },
  [types.DELETE_PLAYLIST] (state, index) {
    state.playlist.splice(index, 1)
  },
  [types.CLEAR_PLAYLIST] (state) {
    state.playlist = []
  },
  [types.SET_PLAY_MODE] (state, mode) {
    state.playMode = mode
  },
  [types.SET_CURRENT_INDEX] (state, index) {
    state.currentIndex = index
  },
  [types.ADD_SEARCH_HISTORY] (state, item) {
    const index = state.searchHistory.indexOf(item)
    if (index >= 0) {
      state.searchHistory.splice(index, 1)
    }
    state.searchHistory.unshift(item)
  },
  [types.DELETE_SEARCH_HISTORY] (state, index) {
    state.searchHistory.splice(index, 1)
  },
  [types.CLEAR_SEARCH_HISTORY] (state) {
    state.searchHistory = []
  },
  [types.ADD_FAVORITE] (state, item) {
    state.favorite.push(item)
  },
  [types.DELETE_FAVORITE] (state, index) {
    state.favorite.splice(index, 1)
  },
  [types.CLEAR_FAVORITE] (state) {
    state.favorite = []
  },
  [types.ADD_PLAY_HISTORY] (state, item) {
    state.playHistory.push(item)
  },
  [types.DELETE_PLAY_HISTORY] (state, index) {
    state.playHistory.splice(index, 1)
  },
  [types.CLEAR_PLAY_HISTORY] (state) {
    state.playHistory = []
  }
}

export default mutations
